@import url("https://fonts.googleapis.com/css2?family=Almarai:wght@300;400;700;800&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@mixin mobile{
  @media screen and (max-width: 480px){
    @content;
  }
}

@mixin tablet{
  @media screen and (max-width: 480px){
    @content;
  }
}
* {
  font-family: "Poppins", sans-serif;

  --text: #ffffff;
  --logo: #3fc99b;
  --grad1: #319e7a;
  --grad2: #059aa5;
  --col1: #4f8158;
  --col2: #609b6a;
  --col3: #586a7a;
  --col4: #334b35;
  --col5: #562a91;
}
html {
  background: linear-gradient(174deg, var(--grad1), var(--grad2));
  background-size: 400% 400%;
  animation: grad 10s ease infinite;
 
}



.formContainer {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  
  @keyframes grad {
    0% {
      background-position: 53% 0%;
    }
    50% {
      background-position: 48% 100%;
    }
    100% {
      background-position: 53% 0%;
    }
  }

  .formWrapper {
    
    background-color: rgb(255, 255, 255);
    padding: 20px 60px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    .logo {
      color: #3fc99b;
      font-weight: bold;
      font-size: 24px;
    }
    .title {
      color: #696969;
    }
    form {
      display: flex;
      flex-direction: column;
     
      gap: 15px;

      input {
        padding: 15px;
        border: none;
        border-bottom: 1px solid #838383;
        width: 250px;
        &::placeholder {
          color: #8f8f8f;
          font-size: 15px;
        }
      }

      .btn {
        
        font-family: "Poppins", "Almarai", sans-serif;
        display: block;
        width: 100%;
        height: 3rem;
        background-color: rgb(15, 23, 42);
        margin-left: auto;
        margin-right: auto;
        border: none;
        font-size: 1.2rem;
        line-height: 1;
        text-align: center;
        cursor: pointer;
        color: var(--text);
        background-color: var(--logo);
        border-radius: 55px;
        box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
        transition: all 0.4s ease 0s;
        outline: none;
      }
      .btn:focus {
        outline: none;
        box-shadow: none;
      }
      .btn:hover {
        transition: all 0.4s ease-in-out;
        background-color: #2b8a6a;
        box-shadow: 0px 5px 10px rgba(7, 14, 12, 0.4);
        color: #ffffff;
      }

      label {
        display: flex;
        align-items: center;
        gap: 10px;
        cursor: pointer;
      }
    }
    p {
      color: #5f5f5f;
      font-size: 15px;
      margin-top: 10px;
    }
  }
}

.search {
  
  .searchForm {
    padding: 10px;

    input {
      background-color: rgba(0, 0, 0, 0);
      border: none;
      width: 100%;
      color: rgb(255, 255, 255);
      outline: none;

      &::placeholder {
        color: white;
      }
    }
  }
}
.chats {
  
  .userChat {
    
    border-top: 1px solid rgba(41, 41, 41, 0.164);
    padding: 10px;
    display: flex;
    align-items: center;
    gap: 10px;
    color: white;
    cursor: pointer;
    transition: all 0.2s;
    
    &:hover {
      background-color: rgba(0, 0, 0, 0.473);
      transition: all 0.2s;
    }
    img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      object-fit: cover;
    }
    
    .userChatInfo {
      
      span {
        font-size: 18px ;
        text-transform: capitalize;
        font-weight: 500;
      }
      p{
          font-size: 14px;
          color: #f8f8f8;

          @include tablet{
            display: none;
          }
      }
    }
  }
}
.home {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  
  .container {
    border: 2px solid rgb(255, 255, 255);
    border-radius: 10px;
    height: 65%;
    width: 80%;
    display: flex;
    overflow: hidden;
    @include tablet{
      height: 100%;
      width: 100%;
    flex-direction: column;
    border: none;
    }
    .sidebar {
      flex: 1;
      background-color: var(--col2);
      border-right: 2px solid rgb(255, 255, 255);

      .navbar {
        display: flex;
        align-items: center;
        background-color: var(--col1);
        height: 50px;
        padding: 10px;

        color: white;
        flex-direction: column;

        .user {
          display: flex;
          justify-content: center;
          gap: 10px;
          width: 100%;
          
          img {
            background-color: #ffffff;
            height: 28px;
            width: 28px;
            border-radius: 50%;
            object-fit: cover;
          }

          button {
            background-color: var(--col4);
            color: white;
            border-radius: 5px;
            border: 0px;
            padding: 4px 6px;
            cursor: pointer;
            margin-left: auto;
          }

          .username {
            align-items: center;
            margin-right: auto;
          }
        }
      }
    }

    .chat {
      flex: 2;
      background-color: var(--col1);

      .chatInfo{
          height: 50px;
          background-color: rgba(0, 0, 0, 0.116);
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 10px;
          color: white;
      }

      .chatIcons{
        display: flex;
        gap:19px;
        

        img{
          height: 32px;
          cursor: pointer;
        }
      }
      .messages{
        background-color: var(--col2);
        padding: 10px;
        height: calc(100% - 100px);
        @include tablet{
          height: calc(100vh - 410px);
          
        }
        z-index: -1;
        overflow-y: scroll;
        
        .message{
          display: flex;
          gap: 20px;
          margin-bottom: 20px;
          .messageInfo{
            display: flex;
            flex-direction: column;
            color: rgb(231, 231, 231);
            font-weight: 300;
            font-size: 14px;
              img{
                width: 40px;
              height: 40px;
              border-radius: 50%;
              object-fit: cover;
              
              }

          }

          .messageContent{
            max-width:80% ;
            display: flex;
            flex-direction: column;
            gap:10px;

            p{
              background-color: white;
              padding: 10px 20px;
              border-radius: 10px 10px 10px 0px;
              max-width: max-content;
            }
            img{
              width: 50%;
              object-fit: cover;
              border-radius: 10px;
            }
          }

          &.owner{
            flex-direction: row-reverse;
            
            .messageContent{
              align-items: flex-end;
            
            p{
              background-color: rgba(0, 0, 0, 0.562);
              color:white;
              border-radius: 10px 10px 0px 10px;
            }
          }
          }
        }
      }

      .input{
        height: 50px;
        background-color: rgb(255, 255, 255);
       padding: 10px;
       z-index: 10;
        display: flex;
        align-items: center;
        justify-content: space-between;
       
        img{
          filter: invert(76%) sepia(0%) saturate(2033%) hue-rotate(6deg) brightness(82%) contrast(71%);
          cursor: pointer;
          height:  28px;
          
          
        }
        
      
        input{
          z-index: 10;
          width: 100%;
          border: none;
          outline: none;
          font-size: 18px;
          color: rgb(0, 0, 0);
          
          
          

          &::placeholder{
            color: #969696;
          }
        }

        .send{
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 10px;
          
          label{
            margin-top: auto;
          }

        }

        button{
          border: none;
          padding: 10px 15px ;
          color: white;
          background-color: var(--col2);
          border-radius: 15px;
          transition: all 0.2s;
          
          &:hover{
            background-color: var(--col1);
            transition: all 0.3s;
          }
        }
      }

    }
  }
}
